<template>
  <div
    id="examin"
    v-if="currenInfo"
  >
    <!-- 员工审核 -->
    <div class="examin-head">
      <img
        class="head-img"
        :src="currenInfo.headUrl"
        alt=""
      >
      <div>
        <div class="f18">{{ currenInfo.name }}</div>
        <div class="c88">{{ currenInfo.phone }}</div>
      </div>
    </div>
    <div class="examin-role">
      <div class="f18">{{ currenInfo?'员工角色':'请配置员工角色' }}</div>
      <div class="role-box">
        <div
          v-for="(item,i) in newRes"
          :key="i"
          class="roles"
          @click="chooseRole(item)"
        >
          <img
            v-if="remove_show == true && item.statu == true"
            class="role-img"
            src="../../assets/gou_open.png"
            alt=""
          >
          <span
            v-else-if="remove_show == true && item.statu == false"
            class="role-img role-kong"
          ></span>
          <span class="role-name">{{ item.dictLabel }}</span>
        </div>
      </div>
    </div>
    <div
      class="examin-role"
      v-if="cleaning_show"
    >
      <div class="f18">保洁任务统计</div>
      <div
        class="role-view"
        @click="goNext('/cleaning-task',currenInfo)"
      >点击查看
        <van-icon name="arrow" />
      </div>
    </div>
    <van-button
      v-if="edit_ok_show"
      class="stsff-button"
      @click="confirm"
    >{{ infoButton }}</van-button>
    <van-button
      v-if="apply_show"
      class="stsff-button"
      @click="confirm"
    >通过申请</van-button>
    <van-button
      v-if="refuse_show"
      class="stsff-button remove-button"
      @click="refuse"
    >拒绝通过</van-button>
    <van-button
      v-if="remove_show && currenInfo.roleArray"
      class="stsff-button remove-button"
      @click="remove"
    >删除员工</van-button>
    <!-- 弹出 -->
    <van-dialog id="van-dialog" />
  </div>
</template>
<script>
import { GetSystemDictDataByType } from '@/api/api_query';
import { PostBusinessHotelOpRelationRoleEdit, PostBusinessAuditApprovalrefuse, GetBusinessCleanorderSum, PostBusinessHotelOpRelationRemove, PostBusinessHotelOpRelationAdd } from '@/api/api_staff';
import { Toast, Dialog } from 'vant';
export default {
    data() {
        return {
            hotelRoles: [],
            obj: {},
            currenInfo: null,
            edit_ok_show: false,
            remove_show: false,
            apply_show: false,
            refuse_show: false,
            cleaning_show: false,
            newRes: null,
            infoButton: '编辑'
        };
    },
    created() {
        this.$store.commit('setHeader', JSON.parse(localStorage.getItem('staffInfo')) ? '员工信息' : '员工审核');
        this.currenInfo = JSON.parse(localStorage.getItem('staffInfo'));
        this.getBytype();
    },
    methods: {
        getBytype() {
            GetSystemDictDataByType({ dictType: 'business_hotel_role' }).done((res) => {
                let newRes = res.data.map((item) => {
                    item.statu = false;
                    return item;
                });
                if (this.currenInfo && this.currenInfo.roleArray) {
                    this.edit_ok_show = true;
                    this.staffRoles = this.currenInfo.roleArray;
                    this.staffRoles.map((item) => {
                        newRes.map((itm) => {
                            if (itm.dictValue == item) {
                                itm.statu = true;
                            }
                        });
                    });
                    this.allNewRes = newRes;
                    this.newRes = newRes.filter((item) => item.statu == true);
                    if (newRes.find((item) => item.dictValue == 'ROLE_HOTEL_CLEANER' && item.statu == true)) {
                        this.cleaning_show = true;
                    }
                } else {
                    this.apply_show = true;
                    this.refuse_show = true;
                    this.remove_show = true;
                    this.newRes = newRes;
                }
            });
        },
        // 点击查看
        goNext(url, item) {
            this.$router.push({
                path: url,
                query: {
                    hotelId: item.hotelId,
                    userId: item.userId
                }
            });
        },
        confirm() {
            if (this.currenInfo.roleArray) {
                if (this.infoButton == '编辑') {
                    this.remove_show = true;
                    this.cleaning_show = false;
                    this.infoButton = '保存';
                    this.newRes = this.allNewRes;
                } else {
                    if (this.newRes.find((item) => item.statu == true)) {
                        let roles = [];
                        this.newRes.map((item) => {
                            if (item.statu == true) {
                                roles.push(item.dictValue);
                            }
                        });
                        let data = {
                            hotelId: this.currenInfo.hotelId,
                            userId: this.currenInfo.userId,
                            roles: roles.join()
                        };
                        PostBusinessHotelOpRelationRoleEdit(data).done((res) => {
                            Toast.success(res.msg);
                            setTimeout(() => {
                                this.$router.go(-1);
                            }, 2000);
                        });
                    } else {
                        Toast('请选择角色');
                    }
                }
            } else {
                if (this.newRes.find((item) => item.statu == true)) {
                    let roles = [];
                    this.newRes.map((item) => {
                        if (item.statu == true) {
                            roles.push(item.dictValue);
                        }
                    });
                    let data = {
                        hotelId: this.currenInfo.hotelId,
                        roles: roles.join(),
                        auditApprovalId: this.$route.query.auditApprovalId,
                        userId: this.$route.query.userId
                    };
                    PostBusinessHotelOpRelationAdd(data).done((res) => {
                        Toast.success(res.msg);
                        setTimeout(() => {
                            this.$router.go(-1);
                        }, 2000);
                    });
                } else {
                    Toast('请选择角色');
                }
            }
        },
        // 拒绝酒店员工申请
        refuse() {
            PostBusinessAuditApprovalrefuse({ auditApprovalId: this.$route.query.auditApprovalId }).done((res) => {
                Toast.success(res.msg);
                setTimeout(() => {
                    this.$router.go(-1);
                }, 2000);
            });
        },
        // 选角色
        chooseRole(item) {
            item.statu = !item.statu;
        },
        // 移除酒店人员
        remove() {
            let c_role = [];
            this.newRes.filter((item) => {
                if (item.statu == true) {
                    c_role.push(item.dictLabel);
                }
            });
            Dialog.confirm({
                title: '确认移除该员工?',
                message: `${c_role.join()}` + ':' + `${this.currenInfo.name}`
            })
                .then(() => {
                    let data = {
                        hotelId: this.currenInfo.hotelId,
                        userId: this.currenInfo.userId
                    };
                    PostBusinessHotelOpRelationRemove(data).done((res) => {
                        Toast.success(res.msg);
                        setTimeout(() => {
                            this.$router.go(-1);
                        }, 2000);
                    });
                })
                .catch(() => {});
        }
    }
};
</script>
<style scoped>
#examin {
    font-size: 0.8rem;
    text-align: left;
    color: #000;
    padding: 20px;
    background: #fff;
    height: 100%;
}
.examin-head {
    display: flex;
    padding-bottom: 10px;
    border-bottom: 1px solid #f4f4f4;
}
.head-img {
    width: 40px;
    height: 40px;
    border-radius: 20px;
    margin-right: 10px;
}
.examin-role {
    margin: 30px 0;
}
.role-view {
    display: flex;
    align-items: center;
    margin: 20px 0;
    color: #ff7d41;
}
.role-box {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin: 20px 0;
    justify-content: space-between;
}
.roles {
    background: #efeff4;
    color: #000;
    width: 48%;
    line-height: 40px;
    margin: 8px 0;
    display: flex;
    align-items: center;
    border-radius: 8px;
    justify-content: center;
}
.role-img {
    width: 20px;
    height: 20px;
    margin: 0 6px;
}
.role-kong {
    border: 1px solid #bbbbbd;
    width: 15px;
    height: 15px;
    border-radius: 10px;
}
.role-name {
    margin: 0 6px;
}
.stsff-button {
    width: 100%;
    background: #ff7d41;
    color: #fff;
    border-radius: 8px;
    margin: 10px 0;
}
.remove-button {
    color: #ff5057;
    background: #fff;
    border: 1px solid #e0e0e0;
}
.f18 {
    font-size: 0.9rem;
    font-weight: bold;
}
.c88 {
    color: #888;
}
</style>



